export default {
  data: function() {
    return {
      jasoData: "",
      columns1: [
        {
          title: "",
          key: "key"
        },
        {
          title: "",
          key: "value"
        }
      ],
      data1: []
    };
  },
  mounted() {
    this.jasoData = {
      agent: "pasture",
      pk: "pkfor5353shanghahsdhjsdgh2382382372837283",
      sk: "shfdjsdfjsdfhdgfye76w7e67w6ergwegruweruweruyweur",
      applicationid: "17286543i3824783423878432374823748234",
      lastuploadstatus: "ok",
    };

    this.dealData();
  },
  methods: {
    dealData: function() {
      for (var key in this.jasoData) {
        var keyValue = {
          key: key,
          value: this.jasoData[key]
        };
        this.data1.push(keyValue);
      }
    }
  }
};



