export default {
  data: function() {
    return {
      blocks: "",
      columns: [
        {
          title: "",
          key: "key"
        },
        {
          title: "",
          key: "value"
        }
      ],
      data: [],
      blocks: []
    };
  },
  mounted() {
    this.blocksData = [
      {
        agent: "牧场",
        blockindex: "0",
        publickey: "styajjsjadhjasdhau682437847234728347",
        time: "20170864",
        data: {
          cattle: "007",
          weather: "cloudy",
          "transport-arriva": "17210174"
        }
      },
      {
        agent: "采购商",
        blockindex: "1",
        publickey: "styajjsjadhjasdhau682437847234728347",
        time: "20170864",
        data: {
          cattle: "007",
          weather: "cloudy",
          "transport-arriva": "17210174"
        }
      },
      {
        agent: "运输商",
        blockindex: "2",
        publickey: "styajjsjadhjasdhau682437847234728347",
        time: "20170864",
        data: {
          cattle: "007",
          weather: "cloudy",
          "transport-arriva": "17210174"
        }
      },
      {
        agent: "货物代理",
        blockindex: "3",
        publickey: "styajjsjadhjasdhau682437847234728347",
        time: "20170864",
        data: {
          cattle: "007",
          weather: "cloudy",
          "transport-arriva": "17210174"
        }
      },
      {
        agent: "检验检疫",
        blockindex: "4",
        publickey: "styajjsjadhjasdhau682437847234728347",
        time: "20170864",
        data: {
          cattle: "007",
          weather: "cloudy",
          "transport-arriva": "17210174"
        }
      },
      {
        agent: "销售商",
        blockindex: "4",
        publickey: "styajjsjadhjasdhau682437847234728347",
        time: "20170864",
        data: {
          cattle: "007",
          weather: "cloudy",
          "transport-arriva": "17210174"
        }
      }
    ];

    this.dealData();
  },
  methods: {
    rowClassName(row, index) {
      if (row.flag) {
        return "demo-table-info-row";
      }
      return "";
    },
    dealData: function() {
      for (var i = 0; i < this.blocksData.length; i++) {
        var data = [];
        for (var key in this.blocksData[i]) {
          if (key == "data") {
            for (var dataKey in this.blocksData[i]["data"]) {
              var keyValue = {
                key: dataKey,
                value: this.blocksData[i]["data"][dataKey],
                flag: true
              };
              data.push(keyValue);
            }
          } else if (key == "blockindex") {
            var keyValue = {
              key: key,
              value: this.blocksData[i][key],
              cellClassName: {
                value: "demo-table-info-cell-index"
              }
            };
            data.push(keyValue);
          } else {
            var keyValue = {
              key: key,
              value: this.blocksData[i][key]
            };
            data.push(keyValue);
          }
        }
        if (i == this.blocksData.length - 1) {
          var blockTmp = {
            data: data,
            columns: this.columns,
            flag: false
          };
        } else {
          var blockTmp = {
            data: data,
            columns: this.columns,
            flag: true
          };
        }
        this.blocks.push(blockTmp);
      }
    }
  }
};



